.App {
    overflow: hidden;
    height: 100vh;
    height: 100dvh;
}
.coverVideo {
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: 50% 50%;
    animation: fadeIn 1s ease-in;
}
.hide {
    animation-name:fadeOut;
    animation-duration:0.5s;
}
/* Main invite */
.invite {
    height: 100%;
    width: 100%;
    border-bottom: 2px solid ;
    border-left: 2px solid ;
    border-right: 2px solid ;
    border-top: 2px solid ;
    position: absolute;
    top:0;
    animation: slideUp 2s ease-in;
}
.coloredBackground {
    height: 100%;
    width: 100%;
    animation: fadeIn 2s ease-in;
    z-index: -10;
}
.animatedBackground {
    position: fixed;
    top: 0;
    overflow-x: hidden;
}
.malaLayer {
    width: 100%;
}
