@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(100%);
        transition: transform 0.5s, opacity 0.5s;
    }
    to {
        opacity: 1;
        transform: translateY(0%);
    }
}

@keyframes malaSway{
    from{transform:translateX(-1px) rotate(0.5deg);}
    to{transform:translateX(1px) rotate(-0.5deg);}
}
@keyframes bellsSway{
    from{transform:translateX(-1px) rotate(1deg); transform-origin: top center;}
    to{transform:translateX(1px) rotate(-1deg); transform-origin: top center;}
}
@keyframes flowersSway{
    from{transform: rotate(1.5deg); transform-origin: top center;}
    to{transform:  rotate(-1.5deg); transform-origin: top center;}
}
